import React, { useState } from 'react';
import './CheckPage.css';
import HomePage from './HomePage'

export default function CheckPage() {
  const [firstInput, setFirstInput] = useState('');
  const [secondInput, setSecondInput] = useState('');
  const [loginStatus, setLoginStatus] = useState('');

  const handleLogin = () => {
    if (firstInput.toLowerCase() === 'honest' && secondInput.toLowerCase() === 'loyal') {
      setLoginStatus('Success');
    } else {
      setLoginStatus('Incorrect values. the first word starts with "h" second with "L" ');
    }
  };

  return (


    <>
    {loginStatus === 'Success' ? (
  
      <HomePage />
    ) : (
      <div id='CheckforContinue'>
    
      <>
        <h2>Solve this to LogIn to this Portal.</h2>
        <h3>Being ______ and ____ to each other matters the most.</h3>

        <div id='formtocontinue'>
          <input
            type="text"
            value={firstInput}
            onChange={(e) => setFirstInput(e.target.value)}
          />
          <span>and</span>
          <input
            type="text"
            value={secondInput}
            onChange={(e) => setSecondInput(e.target.value)}
          />
        </div>

        <button onClick={handleLogin}>Login</button>
        <span className={loginStatus === 'Success' ? 'success' : 'error'}>{loginStatus}</span>
      </>
    
  </div>

    )}
    </>
  );
}
