import { useState } from "react";
import axios from "axios";

import React from "react";
import "./HomePage.css";
import { IoMdMale } from "react-icons/io";
import { IconContext } from "react-icons";
import { IoFemale } from "react-icons/io5";

const AuthPage = (props) => {
  const [lizard, setlizard] = useState("lizard");
  const [lizardpass, setlizardpass] = useState("lizard");

  const [rabbit, setrabbit] = useState("rabbit");
  const [rabbitpass, setrabbitpass] = useState("rabbit");

  const onLogin1 = () => {
 
    axios
    .post("https://vercelbackend-lac.vercel.app/login", { username: lizard, secret: lizardpass })
    .then((r) => props.onAuth({ ...r.data, secret:lizardpass })) // NOTE: over-ride secret
    
  };

  const onLogin2 = () => {
  
    axios
    .post("https://vercelbackend-lac.vercel.app/login", { username: rabbit, secret: rabbitpass })

      .then((r) => props.onAuth({ ...r.data, secret:rabbitpass  })) // NOTE: over-ride secret
    
  };

  return (
    <div id="HomePage">
    <h2 id="title">The Us Project </h2>
    <div id="SelectAccount">
      <div id="twoaccounts"  onClick={onLogin2}>
        <IconContext.Provider
          value={{ color: "white", className: "global-class-name" }}>
            <IoMdMale id="GenderIcon" />
        </IconContext.Provider>
        <h3 id="NameOfUser">Rabbit</h3>
        <span id="hint">Select Rabbit if you are the Rabbit.</span>
        
      </div>
      <div id="twoaccounts" onClick={onLogin1}>
        <IconContext.Provider
          value={{ color: "white", className: "global-class-name" }}>
            <IoFemale id="GenderIcon" />
        </IconContext.Provider>
        <h3 id="NameOfUser">Lizard</h3>
        <span id="hint">Select Lizard if you are the Lizard.</span>
      </div>

    </div>
  </div>
  );
};

export default AuthPage;
